<template>
    <transition @after-leave="afterLeave" name="fade">
        <div v-if="value" class="dialog">
            <div class="dialog__modal">
                <div class="dialog__text">
                    <slot>
                        Are you sure?
                    </slot>
                </div>
                <div class="dialog__buttom">
                    <button @click="cancel" class="btn btn-cancel">cancel</button>
                    <button @click="okay" class="btn btn-okay">okay</button>
                </div>
            </div>
        </div>
    </transition>

</template>

<script>
export default {
    name: "Dialog",
    props: {
        value: Boolean,
    },
    emits: ['okay'],
    data() {
        return {
            statusDialog: false,
            isAccept: null,
        };
    },
    methods: {
        afterLeave() {
            if (this.isAccept) {
                this.$emit('okay');
            }
        },
        cancel() {
            this.$emit('input', false);
            this.isAccept = false;
        },
        okay() {
            this.$emit('input', false);
            this.isAccept = true;
        }
    },
    watch: {
        status(value) {
            this.statusDialog = value;
        }
    }
};
</script>

<style scoped>
.dialog {
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 100;
    background: rgba(40, 37, 37, 0.2);
}

.dialog__modal {
    min-width: 200px;
    position: absolute;
    left: 50%;
    top: 40%;
    transform: translateX(-50%);
    padding: 20px 10px 5px 10px;
    background: rgba(164, 196, 222, 0.76);
    border-radius: 5px;
}


.dialog__text {
    text-align: center;
    font-size: 20px;
    color: #fff;
}

.dialog__buttom {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.fade-enter, .fade-leave-to {
    opacity: 0;
}


.fade-enter-active, .fade-leave-active {
    transition: .6s;
}
</style>