<template>
    <div class="item__wrapper">
        <div @click="openNote" class="item">
            <p class="item__text">
                {{ note.note }}
            </p>
            <div class="wrapper-tasks">
                <div class="tasks" :key="task.id" v-for="task in note.arrTasks">
                    <input v-model="task.status" disabled type="checkbox">
                    <div :class="{'completed--active': task.status}" class="completed">
                        {{ task.task }}
                    </div>
                </div>
            </div>
            <img @click.stop="openDialog" class="cross" src="../assets/img/cross.png" alt="">
        </div>
        <Dialog @okay="deleteNote" v-model="statusDialog">Do you wanna remove it?</Dialog>
    </div>

</template>

<script>
import Dialog from "@/components/Dialog";

export default {
    name: "NoteItem",
    components: {Dialog},
    props: {
        note: Object,
        indexItem: Number,
    },
    data() {
        return {
            statusDialog: false,
        };

    },
    methods: {
        //open note with tasks
        openNote() {
            this.$router.push({name: 'todoList', params: {id: this.note.id}});
        },
        deleteNote() {
            this.$store.commit('deleteNote', this.note.id);
            localStorage.setItem('arrNotes', JSON.stringify(this.$store.state.notes));
        },
        openDialog() {
            this.statusDialog = true;
        }
    }
};
</script>

<style scoped>
.wrapper-tasks {
    padding-left: 20px;
    padding-top: 5px;
}

.item__wrapper {
    transition: all .6s;
    width: 25%;
    padding: 10px;
}

.item {
    overflow: hidden;
    height: 130px;
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.64);
    cursor: pointer;
    padding: 5px 17px 5px 5px;
    background: #bbb5b5;
    border-radius: 7px;
    transition: .6s;
    position: relative;
}

.tasks {
    margin-top: 5px;
    align-items: center;
    display: flex;
}

.completed {
    color: rgba(121, 121, 121, 0.78);
}

.item:hover {
    box-shadow: none;
    transform: scale(1.1);
    background: #87aaec;
    z-index: 2;
}

.item__text {
    padding-bottom: 5px;
    text-align: center;
    border-bottom: 1px solid #fff;
    color: #fff;
    font-size: 24px;
    font-weight: normal;
    font-family: Monospaced;
}
</style>