<template>
    <div class="container">
        <div class="todo">
            <h2 class="title">Todo list</h2>
            <div class="todo__filter">
                <div class="todo__input">
                    <input @keyup.enter="addNewTask" v-model="valueAddingTask" placeholder="New Task"
                           class="custom-input" type="text">
                </div>
                <div class="todo__btn-add">
                    <button @click="addNewTask" class="btn btn-new">Add</button>
                </div>

            </div>
            <div class="todo__note">
                <h2>{{ note.note }}</h2>
            </div>
            <div class="todo__tasks">
                <button @click="cancelChanges" class="btn btn-cancel ">Cancel</button>
                <button @click="saveNote" class="btn btn-save ">Save tasks</button>
            </div>
            <transition mode="out-in" name="opacity">
                <div key="list" v-if="note.arrTasks.length">
                    <transition-group class="todo__items" tag="div" name="task">
                        <todo-item @removeTask="removeTask" @editTask="editTask" :index="index" :task="task"
                                   :key="task.id"
                                   v-for="(task,index) in note.arrTasks"/>
                    </transition-group>
                </div>
                <div key="empty" class="empty" v-else>
                    <h2>You don't have any tasks yet</h2>
                </div>
            </transition>
        </div>
        <Dialog @okay="$router.push({name: 'note'})" v-model="statusDialogCancel">
            <h4>Do you wanna cancel changes?</h4>
        </Dialog>
    </div>
</template>

<script>
import TodoItem from "@/components/TodoItem";
import Dialog from "@/components/Dialog";

export default {
    name: "TodoList",
    components: {Dialog, TodoItem},
    data() {
        return {
            note: JSON.parse(JSON.stringify(this.$store.getters.getCurrentNote(this.$route.params.id))),
            valueAddingTask: '',
            statusDialogCancel: false,
        };
    },
    methods: {
        // cancel changes that user did
        cancelChanges() {
            this.statusDialogCancel = true;
        },
        //save note and redirect to page with notes
        saveNote() {
            this.$store.commit('saveNote', this.note);
            this.$router.push({name: 'note'});
            localStorage.setItem('arrNotes', JSON.stringify(this.$store.state.notes));
        },
        //remove tasks
        removeTask(index) {
            this.note.arrTasks.splice(index, 1);
        },
        //edit task
        editTask(index, nameTask) {
            this.note.arrTasks.splice(index, 1, {...this.note.arrTasks[index], task: nameTask});
        },
        // add new task to array
        addNewTask() {
            this.note.arrTasks.unshift({id: (new Date()).getTime(), task: this.valueAddingTask, status: false});
            this.valueAddingTask = '';
        },
    }

};
</script>

<style scoped>
.todo {
    position: relative;
    overflow: hidden;
}

.todo__filter {
    justify-content: center;
    margin: 10px 0;
    display: flex;
}

.custom-input {
    height: 100%;
    width: 100%;
}

.todo__input {
    flex: 0 0 50%;
}

.todo__tasks {
    text-align: right;
}

.todo__btn-add {
    margin-left: 10px;
}

.btn-cancel {
    margin-right: 5px;
}

.todo__items {
    margin-left: 30px;
}


.todo__item label {
    font-size: 17px;
}


.todo__note h2 {
    display: inline-block;
    border-bottom: 1px solid #000;
    font-family: "Comic Sans MS";
    font-size: 40px;
}

.todo__note {
    text-align: center;
    margin-bottom: 20px;
}

.task-enter, .task-leave-to {
    opacity: 0;
    transform: translateX(100%);
}


.task-leave-active {
    position: absolute;
}


</style>