export default {
    state: {
        notes: [],
    },
    mutations: {
        addingNewNote(state, payload) {
            state.notes.unshift(payload);
        },
        deleteNote(state, id) {
            state.notes = state.notes.filter(e => e.id !== id);

        },
        saveNote(state, payload) {
            const index = state.notes.findIndex(e => e.id === payload.id);
            state.notes[index].arrTasks = payload.arrTasks;
        },
        getFromLocalStorage(state) {
            if (localStorage.getItem('arrNotes')) {
                state.notes = JSON.parse(localStorage.getItem('arrNotes'));
            }
        }
    },
    getters: {
        getCurrentNote: (state) => (id) => {
            return state.notes.find(todo => todo.id === +id);
        }
    }
};