<template>
    <div class="wrapper">
        <div class="todo__item">
            <input v-model="task.status" :id="task.id" type="checkbox">
            <div :class="{'completed--active' : task.status }" class="completed">
                <label :title="task.task" :for="task.id">{{ task.task }}</label>
            </div>
            <div class="todo__btns-tasks">
                <button @click="openDialogEdit" class="btn btn-okay todo__btn-edit">edit</button>
                <button @click="openDialogRemove" class="btn btn-cancel">remove</button>
            </div>
            <Dialog @okay="editTask" v-model="statusDialogEdit">
                <h4>Rename the task</h4>
                <input v-model="renameTask" class="custom-input" type="text">
            </Dialog>
            <Dialog @okay="removeTask" v-model="statusDialogRemove">
                <h4>Do you wanna remove the task?</h4>
            </Dialog>
        </div>
    </div>
</template>

<script>
import Dialog from "@/components/Dialog";

export default {
    name: "TodoItem",
    components: {Dialog},
    props: {
        task: Object,
        index: Number,
    },
    emits: ['editTask'],
    data() {
        return {
            renameTask: '',
            statusDialogEdit: false,
            statusDialogRemove: false,
        };
    },
    methods: {
        //Open dialog that remove tasks
        openDialogRemove() {
            this.statusDialogRemove = true;
        },
        removeTask() {
            this.$emit('removeTask', this.index);
        },
        editTask() {
            this.$emit('editTask', this.index, this.renameTask);
            this.renameTask = '';
        },
        //Open dialog that editing tasks
        openDialogEdit() {
            this.statusDialogEdit = true;
            this.renameTask = this.task.task;
        }
    }
};
</script>

<style scoped>


.todo__btns-tasks {
    margin-left: auto;
}

.todo__btn-edit {
    margin-right: 5px;
}

.todo__item {
    margin: 5px 0;
    padding: 4px;
    border: 1px solid rgba(147, 144, 144, 0.75);
    display: flex;
    align-items: center;
}

.wrapper {
    width: 100%;
    transition: all .6s;
}

</style>