<template>
    <div class="container">
        <div class="note">
            <h2 class="title">
                Notes
            </h2>
            <div class="note__add">
                <button @click="openDialog" class="btn btn-new">Add new note</button>
            </div>

            <transition mode="out-in" name="opacity">
                <div key="note" v-if="arrNotes.length">
                    <transition-group name="list" tag="div" class="note_items">
                        <note-item :key="item.id" :index-item="index" :note="item"
                                   v-for="(item,index) in arrNotes"/>
                    </transition-group>
                </div>
                <div key="empty" v-else class="empty">
                    <h4>You don't have any notes yet</h4>
                </div>
            </transition>
        </div>
        <Dialog @okay="addNewNote" v-model="statusDialog">
            <div>
                Enter name for the note
            </div>
            <input @keyup.enter="addNewNote" v-model="valueNote" class="custom-input" type="text">
        </Dialog>
    </div>
</template>

<script>


import NoteItem from "@/components/NoteItem";
import Dialog from "@/components/Dialog";

export default {
    name: "Note",
    components: {Dialog, NoteItem},
    data() {
        return {

            statusDialog: false,
            valueNote: '',
        };
    },
    computed: {
        arrNotes() {
            return this.$store.state.notes;
        }
    },
    methods: {

        openDialog() {
            this.statusDialog = true;
        },

        //adding new note and set to localStorage
        addNewNote() {
            const newNote = {
                note: this.valueNote,
                id: (new Date()).getTime(),
                arrTasks: [],
            };
            this.$store.commit('addingNewNote', newNote);
            this.valueNote = '';
            localStorage.setItem('arrNotes', JSON.stringify(this.arrNotes));
            this.$router.push({name: 'todoList', params: {id: String(newNote.id)}});
        },

    }
};
</script>

<style scoped>
.note {
    position: relative;
}

.container {
    height: 80vh;
}


.note__add {
    text-align: center;
    margin-top: 10px;
}

.note_items {
    margin-top: 10px;
    display: flex;
    flex-wrap: wrap;
}

.list-enter, .list-leave-to {
    opacity: 0;
}


.list-leave-active {
    position: absolute;
}


</style>